import { useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { useNavigate } from 'react-router-dom';
import { WalletApi, WithdrawInitApi  } from 'utils/Api';

const Wallet = () => {
  const navigate = useNavigate();
  const { t } = useTranslation();
  const [finacialData, setFinacialData] = useState([]);
  const [walletData, setWalletData] = useState({});
  const [allPage, setAllPage] = useState(1);
  const [tableData, setTableData] = useState([]);

  useEffect(() => {
    WalletApi().then((res) => {
      setFinacialData(
        res.data.finacial.map((data) => ({
          ...data,
          createdAt: data.createdAt
            .replace(/[TZ]/g, ' ')
            .substr(0, data.createdAt.length - 5),
        }))
      );
      setWalletData(res.data.wallet);
    });
  }, []);

  useEffect(() => {
    setAllPage(Math.ceil(finacialData.length / 12));
    setTableData(finacialData.slice(0, 12));
  }, [finacialData]);

  const handlePage = (index) => {
    if (index === 0) {
      setTableData(finacialData.slice(0, 12));
    } else {
      setTableData(finacialData.slice(index * 12, index * 12 + 12));
    }
  };

  const handleWithdraw = () => {
		WithdrawInitApi().then((res) => {
			console.log(res.data);
			
		})
    // navigate('/withdraw');
  };

  return (
    <div className="flex flex-col justify-center  gap-2 h-full">
      <div className=" bg-teal-600 h-36 p-4 m-2 rounded-3xl">
        <div className="text-gray-200  ">{t('my_blance')}</div>
        <div className="text-white font-bold text-center text-3xl mt-4 mb-4 ">
          {walletData.blance}
        </div>
        <div className="flex justify-between text-xs text-white">
          <p className="">
            {t('my_income')}：{walletData.income}
          </p>
          <p className="">
            {t('my_withdraw')}：{walletData.withdraw}
          </p>
        </div>
      </div>
      <div className=" flex-auto">
        <table className="min-w-full">
          <thead >
            <tr>
              <th
                scope="col"
                className="text-sm font-bold text-gray-900  text-center"
              >
                名称
              </th>
              <th
                scope="col"
                className="text-sm font-bold text-gray-900 text-center"
              >
                金额
              </th>
              <th
                scope="col"
                className="text-sm font-bold text-gray-900 text-center"
              >
                交易时间
              </th>
            </tr>
          </thead>
          <tbody className="">
            {tableData.map((data, index) => (
								
                <tr key={index}>
                  <td className="px-2 pt-2 font-bold text-black-600">
                    {data.description}
                  </td>
                  <td className="text-right px-2 py-1 font-bold text-teal-600">
                    {data.amount}
                  </td>
                  <td className="text-right px-2 ">
                    {data.createdAt.replace(/[TZ]/g, ' ')}
                  </td>
                </tr>
            ))}
          </tbody>
        </table>
        <ul className="flex justify-center items-center mt-4">
          {allPage !== 1 &&
            [...Array(allPage)].map((_, index) => (
              <li
                key={index}
                className="m-1 p-1 text-teal-600  border-b-solid border"
                onClick={() => handlePage(index)}
              >
                {index + 1}
              </li>
            ))}
        </ul>
      </div>
      <div className="h-24 flex justify-between p-3">
        <button
          className=" ml-2 w-36 rounded-3xl border-solid border-2 border-teal-500 text-teal-500"
          onClick={handleWithdraw}
        >
          {t('want_withdraw')}
        </button>
        <button
          className="mr-2 w-36 rounded-3xl border-solid border-2 border-orange-500 text-orange-500"
          onClick={() => navigate('/recharge')}
        >
          {t('want_recharge')}
        </button>
      </div>
    </div>
  );
};

export default Wallet;
